<template>
  <div class="info">
    <span v-html="title"></span>
    <ul>
      <li><strong>Who am I?</strong> My name is <a href="https://twitter.com/mda590" title="Twitter @mda590" target="_blank">Matt</a>.
        I'm an AWS architect, and overall fan of all things cloud.</li>
      <li><strong>Why did I create this?</strong> Over time, as I've worked on global AWS deployments, I have often been faced
        with the question of which inter-region transactions will be faced with the most latency. I have been able to find
        a lot of static examples of previous testing completed, or anecdotal thoughts based on a region's location. I haven't
        been able to find any kind of dynamic, consistently updated, latency monitoring. The goal here is to provide a single
        source of truth for inter-region AWS region latency.
        <br />I also created this as a way to play around with creating and deploying an API and purely serverless application.
      </li>
      <li><strong>Please get in touch</strong> if you have any comments or suggestions. You can also use <a href="https://github.com/mda590/cloudping.co/issues"
          title="GitHub Issues" target="_blank">GitHub Issues</a> to submit comments, questions, or new feature requests.</li>
      <li><strong>More details on the project</strong> can be found in the <a href="https://github.com/mda590/cloudping.co" title="GitHub"
          target="_blank">GitHub README</a>. This project is in no way associated with Amazon or AWS.</li>
    </ul>
</div>
</template>

<script>
  export default {
    name: 'info',
    data () {
      return {
        title: '<h3>About this Utility</h3>'
      }
    }
  }
</script>

<style scoped>
.info {
    border: 2px solid #42b983;
    border-radius: 3px;
    background-color: #f9f9f9;
    width: 99%;
    margin-top: 20px;
    padding-left: 10px;
    text-align: left;
}
li {
    font-size: 14px;
}
</style>


