<template>
  <div class="averages">
    <div align="right"><iframe src="https://ghbtns.com/github-btn.html?user=mda590&repo=cloudping.co&type=star&count=true" frameborder="0" scrolling="0" width="170px" height="20px"></iframe></div>
    <span v-html="title"></span>
    <div id="note">
      <p><em>The data in the table below represents the averages of the previous 24 hours of data collected.</em></p>
      <p><em>For more information on this process, visit the <a href="https://github.com/mda590/cloudping.co" title="GitHub" target="_blank">GitHub README.</a></em></p>
    </div>
    <table align="center">
      <thead>
        <tr>
          <th><p v-show="loading">Loading...</p><em v-show="!loading" style="font-size: 10px">All times are in milliseconds.</em></th>
          <th v-for="region in averages">{{region.region}}</th>
        </tr>
      </thead>
      <tr v-for="region in averages">
        <th>{{region.region}}</th>
        <td v-if="region.region != average.regionTo" v-for="average in region.averages">
          <span v-if="average.average < 100" class="green"></span>
          <span v-else-if="average.average <= 180 && average.average >= 100" class="yellow"></span>
          <span v-else-if="average.average > 180" class="red"></span>
          {{average.average.toFixed(2)}}
        </td>
        <td v-else style="background-color: #000"></td>
      </tr>
    </table>
    <div align="center" class="colorlegend">
      <span>Latency:</span>
      <span class="green"> < 100ms</span>
      <span class="yellow"> 100-180ms </span>
      <span class="red"> > 180ms </span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'averages',
    data () {
      return {
        title: '<h1>AWS Inter-Region Latency</h1>',
        averages: [],
        loading: true
      }
    },
    created: function () {
      this.$http.get('https://api.cloudping.co/averages')
        .then(function (response) {
          this.averages = response.data
          this.loading = false
        })
    }
  }
</script>

<style scoped>
  body {
    font-family: Helvetica Neue, Arial, sans-serif;
    font-size: 11px;
    color: #444;
  }

  table {
    border: 2px solid #42b983;
    border-radius: 3px;
    background-color: #fff;
    width: 100%;
  }

  th {
    background-color: #42b983;
    color: #fff;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  td {
    background-color: #f9f9f9;
    text-align: left;
  }

  th, td {
    padding: 5px 5px 5px 5px;
  }
  #note {
    font-size: 12px;
    margin: -10px 0px 20px 0px;
  }
  #note p {
    margin: 0px;
  }
  .red:before {
    content: ' \25CF';
    font-size: 20px;
    color: #ff3300;
    padding-left:5px;
  }
  .yellow:before {
    content: ' \25CF';
    font-size: 20px;
    color: #e6e600;
    padding-left:5px;
  }
  .green:before {
    content: ' \25CF';
    font-size: 20px;
    color: #00e600;
    padding-left:5px;
  }
  .colorlegend {
    font-size: 12px;
    padding-top: 5px;
  }

</style>


